.header {
  width: 100%;
  min-width: 1226px;
  height: 80px;
  color: #fff;
  background-color: #000;
}
.header a {
  color: #fff;
  display: inline-block;
}
.header .headerBar {
  height: 100%;
  width: 1226px;
  margin: 0 auto;
  font-size: 1.3em;
  font-weight: 400;
}
.header .headerBar .headerList {
  float: left;
  margin-top: 25px;
}
.header .headerBar .headerList li {
  display: inline-block;
  list-style: none;
  margin-right: 40px;
}
.header .headerBar .drawer {
  display: none;
}
.header .headerBar .headerRight {
  float: right;
  font-size: 0.8em;
}
.header .headerBar .headerRight .name {
  display: inline-block;
  margin: 30px 20px;
}
.header .headerBar .headerRight .drawer {
  display: inline-block;
  float: right;
}
.header .headerBar .headerRight .drawer .avatar {
  float: right;
  margin: 10px 40px 10px 10px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.header .headerBar .headerList2 {
  float: right;
  margin-top: 25px;
  margin-right: 40px;
}
.header .headerBar .headerList2 li {
  display: inline-block;
  list-style: none;
  margin-left: 40px;
}
