.post-list {
  width: 1226px;
  padding: 30px;
  margin: 0 auto;
}
.post-list .postid {
  float: left;
  padding: 20px;
  border: 1px solid #e8e8e8;
  border-radius: 10px;
  width: 826px;
}
.post-list .postid .title {
  display: inline-block;
  width: 580px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: bold;
  color: #8d64ec;
}
.post-list .postid .content {
  width: 580px;
  margin-left: 45px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.post-list .sidebar {
  float: right;
  width: 300px;
  border-radius: 10px;
}
.post-list .sideArtical {
  float: right;
  width: 300px;
  margin-top: 30px;
}
.post-list .sideArtical a {
  color: #d1830e;
}
