.personal {
  width: 1226px;
  margin: 20px auto;
  height: calc(100% - 120px);
}
.personal .sideBar {
  width: 300px;
  height: 100%;
  float: left;
  border: 1px solid #d9d9d9;
  border-radius: 30px;
}
.personal .sideBar .info {
  width: 300px;
  height: 260px;
  margin: 20px 0 40px;
  padding: 40px;
  text-align: center;
}
.personal .sideBar .info div {
  width: 100px;
  margin: 0 auto 20px;
}
.personal .sideBar .info div .avatar {
  width: 100%;
}
.personal .sideBar .info p {
  font-size: 16px;
  margin: 0;
}
.personal .sideBar .line {
  border: 0.5px solid #d9d9d9;
}
.personal .sideBar .bar {
  width: 100%;
}
.personal .sideBar .bar .barBtn {
  width: 100%;
  height: 60px;
  border: 0;
  font-size: 20px;
}
.personal .view {
  width: 900px;
  overflow: hidden;
  float: right;
  padding: 20px;
  border: 1px solid #d9d9d9;
  border-radius: 30px;
}
