.login {
  padding: 20px;
  border: 10px solid #eee;
  border-radius: 30px;
  width: 360px;
  height: 280px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  box-sizing: border-box;
}
.login .form {
  width: 300px;
  height: 340px;
}
.login .form .avatar-uploader .ant-img {
  max-height: 40px;
}
.login .form .avatar-uploader .ant-upload {
  width: 298px;
  height: 60px;
}
.login .form .avatar-uploader .ant-upload-text {
  font-size: 13px;
  opacity: 0.5;
  margin-left: 10px;
}
.login .form .toggle {
  float: right;
}
.login .form .button {
  width: 100%;
}
