.postview {
  width: 1226px;
  margin: 30px auto;
  border: 1px solid #e8e8e8;
  border-radius: 30px;
  padding: 30px;
  overflow: hidden;
}
.postview .sidebar {
  float: left;
  width: 300px;
  overflow: hidden;
  text-align: center;
}
.postview .sidebar .avatar {
  margin: 15px;
}
.postview .sidebar .avatar img {
  width: 150px;
  margin: 50px;
}
.postview .sidebar .avatar p {
  font-size: 1.2em;
  color: #32420dd2;
}
.postview .sidebar .avatar .btn {
  width: 150px;
  margin: 50px;
}
.postview .artical {
  float: right;
  width: 850px;
  overflow: hidden;
  padding: 30px;
}
.postview .artical > h1 {
  text-align: center;
  margin-bottom: 30px;
  padding-bottom: 30px;
  border-bottom: 5px solid #f2f2f2;
  color: #67e0cc;
}
